.hero {
	flex-direction: column;
	justify-content: flex-end;
	color: var(--gray-300);

	&,
	&-bottom {
		display: flex;
	}

	&-bottom {
		flex-direction: column;
		align-items: center;
		gap: 3rem;

		@media (min-width: 992px) {
			flex-direction: row;
			align-items: flex-end;
			justify-content: space-between;
			gap: initial;
		}
	}

	&-info,
	&-footer {
		flex-shrink: 0;

		@media (min-width: 992px) {
			width: 360px;
		}
	}

	&-info {

		@media (max-width: 992px) {
			text-align: center;
			position: absolute;
			left: 50%;
			top: calc(50% - 1rem);
			transform: translate(-50%, -50%);
		}

		&-title {
			font-size: 1.75rem;
			font-weight: 300;
			color: var(--gray-200);
			text-transform: uppercase;
			margin-bottom: .5rem;

			@media (min-width: 992px) {
				font-size: 3.75rem;
				margin-bottom: unset;
				position: absolute;
				left: 50%;
				top: calc(50% - 1rem);
				transform: translate(-50%, -50%);
			}
		}

		&-text {
			font-style: italic;
			margin-bottom: 1rem;

			@media (min-width: 992px) {
				margin-bottom: 1.25rem;
			}
		}
	}

	&-footer {
		display: flex;
		flex-direction: column;
		font-size: .625rem;

		@media (min-width: 992px) {
			font-size: .75rem;
			align-items: flex-end;
		}

		.btn-link {
			--_fs: .875rem;
			--_color: inherit;

			@media (max-width: 992px) {
				display: none;
			}

			+ .hero-footer-copy {
				
				@media (min-width: 992px) {
					margin-top: .75rem;
				}
			}
		}

		&-copy {
			text-align: center;

			@media (min-width: 992px) {
				text-align: end;
			}
		}
	}
}