.main-swiper {
	height: 100vh;
	height: 100svh;
	height: 100dvh;

	&-slide {
		height: 100%;
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center;
		padding-block: 3rem;

		> .container {
			height: 100%;
		}

		&-bg {
			width: 100%;
			height: 100%;
			display: block;
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
			
			&-wrapper {
				width: 100%;
				box-shadow: .25rem .25rem 2rem 1rem rgba(0, 0, 0, 0.25);
				user-select: none;
				pointer-events: none;
				position: absolute;
				inset: 0;
				z-index: -1;

				@media (min-width: 992px) {
					width: 45%;
				}
			}
		}

		&-container {
			display: flex;
			flex-direction: column;
			justify-content: flex-end;
		}

		&-hint {
			display: flex;
			flex-direction: column;
			align-items: center;

			.icon-mouse,
			.icon-down {
				
				@media (hover: none) {
					display: none;
				}
			}

			.icon-mouse {
				--_size: 2.25rem
			}

			.icon-down {
				--_size: 1.25rem;

				@media (hover: hover) {
					animation: bounce 3s infinite ease-in-out;
				}
			}

			.icon-swipe {
				--_size: 3rem;

				@media (hover: hover) {
					display: none;
				}

				@media (hover: none) {
					animation: swipe-right 2s infinite ease-in-out;
				}
			}
		}
	}
}

@keyframes bounce {
  0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(.5rem);
  }
  60% {
    transform: translateY(.25rem);
  }
}

@keyframes swipe-right {
  0%, 20% {
    transform: translateX(0);
  }
  60% {
    transform: translateX(-.5rem) rotate(-20deg);
  }
  100% {
    transform: translateX(0);
  }
}