.section {

	&-header {
		margin-bottom: 1rem;
	}

	&-title,
	&-subtitle {
		color: var(--gray-400);
		text-transform: uppercase;
	}

	&-title {
		display: flex;
		align-items: center;
		gap: .25em;

		font-size: 2.5rem;
		font-weight: 600;
		letter-spacing: .25em;

		@media (min-width: 992px) {
			font-size: 5rem;
		}

		@media (min-width: 1200px) {
			font-size: 6rem;
		}

		@media (min-width: 1400px) {
			font-size: 8.5rem;
		}

		&-start {
			
			@media (min-width: 992px) {
				width: 45%;
			}
		}

		&-end {
			
			@media (min-width: 992px) {
				width: 51.5%;
				margin-left: auto;
			}
		}
	}

	&-subtitle {
		font-size: 1.5rem;

		@media (min-width: 1400px) {
			font-size: 2.25rem;
		}

		@media (min-width: 1200px) {
			font-size: 1.75rem;
		}
	}
}