h1, h2, h3, h4, h5, p {
	margin: 0;
}

h1, h2, h3, h4, h5 {
	letter-spacing: .625em;
}

p {

	+ p {
		margin-top: 1rem;
	}
}

.color-gray-200 {
	color: var(--gray-200) !important;
}

.color-gray-300 {
	color: var(--gray-300) !important;
}

.color-gray-400 {
	color: var(--gray-400) !important;
}

@media (max-width: 992px) {
	.color-gray-200-lg {
		color: var(--gray-200) !important;
	}

	.color-gray-300-lg {
		color: var(--gray-300) !important;
	}

	.color-gray-400-lg {
		color: var(--gray-400) !important;
	}
}

b {
	font-weight: 600;
}

.object-lens {
	display: inline-block;
	padding-left: .25em;
	position: relative;

	.object-lens-decor,
	.icon {
		position: absolute;
	}

	.object-lens-decor {
		display: flex;
		align-items: center;
		gap: .625em;

		font-size: .125em;
		letter-spacing: 0;
		top: 0;
		left: 0;

		&::before {
			content: '';
			display: block;
			width: 1em;
			height: 1em;
			background-color: currentColor;
			border-radius: 50%;
			animation: object-lens 2s infinite alternate;
		}
	}

	.icon {
		--_size: .5em;

	}

	.icon-border-top-right {
		top: 0;
		right: .0625em;
	}

	.icon-border-bottom-left {
		left: 0;
		bottom: 0;
	}
}

@keyframes object-lens {
  0% {
    background-color: currentColor;
  }
  50% {
    background-color: red;
  }
  100% {
    background-color: currentColor;
  }
}