.header {
	color: var(--gray);
	padding-block: 3rem;
	pointer-events: none;
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
	z-index: 1100;

	&-container {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		gap: 4rem;
		text-align: end;

		.nav {

			@media (max-width: 992px) {
				display: none;
			}

			&__list {
				display: flex;
				flex-direction: column;
				gap: .5rem;

				> * {
					&:first-child {
						display: none;
					}
				}
			}
		}

		.btn {
			--_color: inherit;

			pointer-events: auto;
		}
	}
}